
  <template>
    <div id="dashboard">
      <ul class="collection with-header">
        <li class="collection-header">
          <h4>Tasks</h4>
        </li>
        <li v-for="task in tasks" v-bind:key="task.id" class="collection-item">
          {{task.task_number}}: {{task.task_name}}

          <button @click="deleteTask(task), addImage()" class="btn red" style="float: right">
          Complete task</button>

          <input
            type="hidden"
            role="uploadcare-uploader"
            name="content"
            data-public-key="demopublickey"
            data-image-shrink="falsexfalse"
            data-preview-step="true"
            data-multiple="true"/>
        </li>
      </ul>



      <div class="fixed-action-btn">
        <router-link to="/new" class="btn-floating
        btn-large red">
          <i class="fa fa-plus"></i>
        </router-link>
      </div>
    </div>
  </template>


<script>
import database from './firebaseinit'
/*import config from './firebaseConfig'

firebase.initializeApp(config)
const firestore = firebase.firestore()

const settings = {/* your settings... timestampsInSnapshots: true};
firestore.settings(settings);*/



  export default {
    name: 'dashboard',
    data(){
      return {
        tasks: [],
      }

    },
    created(){
      database.collection('tasks').orderBy('task_number').get().then(querySnapshot => {
        querySnapshot.forEach(doc => {
          const data = {
            'id': doc.id,
            'task_name': doc.data().task_name,
            'task_number': doc.data().task_number,
            'completed': doc.data().completed,
          }
          this.tasks.push(data)
        })
       })
    },
    methods: {
      deleteTask(task){
        if(confirm('Are you sure?')){
          console.log(task.task_name)
          console.log(task.task_number)
          console.log(task.completed)
          database.collection("tasks").doc(task.id).delete().then(function(){

            location.reload()

          })
          //const db = database.ref('tasks')
          //taskRef.child(task).remove()
        }
      },
      addImage(){
        const url = "http://api.giphy.com/v1/gifs/search?q=unicorn&api_key=DBCfPhi0wvbUxmmKfZ9Iueib8CikwIJP&limit=100"
        let gifID = null
        let xhr = $.get(url);
        xhr.done(function(data) {
          for (var i = 0; i < data.data.length; i++) {
            var data1 = data.data[i]
            gifID=data1.images.original.url
          }

          console.log("gifID",gifID)
          var newImg = document.createElement("IMG")
          newImg.setAttribute("src",gifID)
          console.log("we did it",gifID)
          document.getElementById("dashboard").appendChild(newImg)
          console.log("gifID2",gifID)

          })



    //console.log("ejemplo", xhr[0]);
      }
    }
}

</script>



