import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/Dashboard'
import NewTask from '@/components/NewTask'
import Gif from '@/components/Gif'
//import Auth from '@/components/Auth'

Vue.use(Router)

export default new Router({
  routes:[
    /*{
      path: '/auth',
      name: 'auth',
      component: Auth
    },*/
    {
      path: '/',
      name: 'dashboard',
      component: Dashboard
    },
    {
      path: '/new',
      name: 'new-task',
      component: NewTask
    },
    {
      path: '/task_completed',
      name: 'gif',
      component: Gif
    }
  ]
})



